import React, { Component } from 'react';
import Button from '../../../formComponents/Button';
import Select from '../../../formComponents/Select';
import Date from '../../../formComponents/Date';

import './_Form.scss';

const model = require('../../../../utils/model/__Model');
const optionsNatureza = model.optionsNatureza;
const optionsComposicaoPista = model.optionsComposicaoPista;
const optionsDiaSemana = model.optionsDiaSemana;
const optionsLuminosidade = model.optionsLuminosidade;
const optionsCondicaoTempo = model.optionsCondicaoTempo;
const optionsClassificacaoAcidente = model.optionsClassificacaoAcidente;
const optionsVelocidade = model.optionsVelocidade;

const optionsMotivo = model.optionsMotivo;
const optionsTipo = model.optionsTipo;

export default class Form extends Component {

    constructor(props) {
        super(props);

        this.state = {
            getServerData: this.props.getServerData,

            natureza: optionsNatureza[0] || 'Todos',
            composicaoPista: optionsComposicaoPista[0] || 'Todos',
            diaSemana: optionsDiaSemana[0] || 'Todos',
            luminosidade: optionsLuminosidade[0] || 'Todos',
            condicaoTempo: optionsCondicaoTempo[0] || 'Todos',
            classificacaoAcidente: optionsClassificacaoAcidente[0] || 'Todos',
            velocidade: optionsVelocidade[0] || 'Todos',
            dataInicio: 'Todos',
            dataFim: 'Todos',

            //medical-occurrences filters
            motivo: optionsMotivo[0] || 'Todos',
            tipo: optionsTipo[0] || 'Todos',
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    onSubmit(e) {
        e.preventDefault();
    }

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    handleClick(id, inputData) {
        this.setState({ [id]: inputData });
    }

    render() {

        const { mapOpacity, mapRadius, getServerData, motivo, tipo, ...formData } = this.state;
        const { dataInicio, dataFim } = formData;

        if(this.props.database === 'accidents'){
            return (
                <form id='form' onSubmit={this.onSubmit} >
                    <h2>Filtros</h2>
                    <Select
                        id='natureza'
                        value={this.state.natureza}
                        title='Natureza'
                        options={optionsNatureza}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='composicaoPista'
                        value={this.state.composicaoPista}
                        title='Composição da Pista'
                        options={optionsComposicaoPista}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='diaSemana'
                        value={this.state.diaSemana}
                        title='Dia da Semana'
                        options={optionsDiaSemana}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='luminosidade'
                        value={this.state.luminosidade}
                        title='Luminosidade'
                        options={optionsLuminosidade}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='condicaoTempo'
                        value={this.state.condicaoTempo}
                        title='Condição do Tempo'
                        options={optionsCondicaoTempo}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='classificacaoAcidente'
                        value={this.state.classificacaoAcidente}
                        title='Classificação'
                        options={optionsClassificacaoAcidente}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='velocidade'
                        value={this.state.velocidade}
                        title='Velocidade da Via'
                        options={optionsVelocidade}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Date
                        name='dataInicio'
                        title='Data de Inicio'
                        onChange={this.handleChange}
                    />
    
                    <Date
                        name='dataFim'
                        title='Data Final'
                        onChange={this.handleChange}
                    />
    
                    <Button
                        class='filter-button'
                        onClick={() => getServerData(formData)}
                        val='Filtrar'
                    />
    
                    <div
                        className={`filtering-container ${this.props.filtering ? 'loading' : ''}`}>
                        <span className='filtering-text'>Filtrando</span>
                        <img src="/images/loading.svg" alt="loading-gif" />
                        <span className='gif-attr'>Imagem disponível em: loading.io/spinner/ellipsis</span>
                    </div>
                </form>
            );
        }
        else if(this.props.database === 'medical-occurrences'){
            return (
                <form id='form' onSubmit={this.onSubmit} >
                    <h2>Filtros</h2>
                    <Select
                        id='motivo'
                        value={this.state.motivo}
                        title='Motivo'
                        options={optionsMotivo}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />
    
                    <Select
                        id='tipo'
                        value={this.state.tipo}
                        title='Tipo'
                        options={optionsTipo}
                        onChange={this.handleChange}
                        onClick={this.handleClick}
                    />

                    <Date
                        name='dataInicio'
                        title='Data Inicial'
                        onChange={this.handleChange}
                    />
                    <Date
                        name='dataFim'
                        title='Data Final'
                        onChange={this.handleChange}
                    />
    
                    <Button
                        class='filter-button'
                        onClick={() => getServerData({ motivo, tipo, dataInicio, dataFim })}
                        val='Filtrar'
                    />
    
                    <div
                        className={`filtering-container ${this.props.filtering ? 'loading' : ''}`}>
                        <span className='filtering-text'>Filtrando</span>
                        <img src="/images/loading.svg" alt="loading-gif" />
                        <span className='gif-attr'>Imagem disponível em: loading.io/spinner/ellipsis</span>
                    </div>
                </form>
            );
        }
    }
}