import React, { Component } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';

import './_Details.scss';

import { ip } from '../../utils/api/url';

export default class Info extends Component {

    constructor(props) {
        super(props);

        this.state = {
            response: []
        }

        this.getData = this.getData.bind(this);
    }

    componentDidMount() {
        this.getData( );
    }

    showContainer() {
        const container = document.querySelector('#details-container');
        const image = document.querySelector('.loading');

        image.classList.add('hide-image');
        container.classList.add('show-container');
    }

    getData() {
        const { handle, database } = this.props.match.params;
        const token = Cookies.get('token');

        axios.get(`${ ip }/${ database }/${ handle }`, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: token
            },
            withCredentials: true,
        }).then((res) => {
            this.setState({ response: res.data });
        }).then(() => {
            this.showContainer();
        }).catch(error => {
            console.log(error);
        });
    }

    render() {

        const { database } = this.props.match.params;

        if(database === 'medical-occurrences'){
            return(
                <React.Fragment>
                    <div className='details-container' id='details-container'>
                        {
                            this.state.response.length > 0
                                ? <div className='inside'>
                                    <header>
                                        <h1>Detalhes</h1>
                                        <button onClick={() => this.props.history.goBack() }>
                                            Voltar
                                        </button>
                                    </header>
                                    <div className="data-container">
                                        <div className="category">
                                            <h2>Informações da Ocorrência</h2>
                                            <div className="info-container">
                                                <span>Número da Ocorrência:</span>
                                                <span>
                                                    {this.state.response[0].properties.numero}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Motivo:</span>
                                                <span>
                                                    {this.state.response[0].properties.motivo}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Tipo:</span>
                                                <span>
                                                    {this.state.response[0].properties.tipo}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Data:</span>
                                                <span>
                                                    {this.state.response[0].properties.data}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Hora:</span>
                                                <span>
                                                    {this.state.response[0].properties.hora}
                                                </span>
                                            </div>
                                        </div>
                                        <div className="category">
                                            <h2>Informações do Local</h2>
                                            <div className="info-container">
                                                <span>Endereço:</span>
                                                <span>
                                                    {this.state.response[0].properties.endereco}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Latitude:</span>
                                                <span>
                                                    {this.state.response[0].properties.latitude}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Longitude:</span>
                                                <span>
                                                    {this.state.response[0].properties.longitude}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                : null
                        }
                    </div>
                    <img className='loading' src="/images/loading.svg" alt="loading" />
                </React.Fragment>
            );
        }
        else{
            return (
                <React.Fragment>
                    <div className='details-container' id='details-container'>
                        {
                            this.state.response.length > 0
                                ? <div className='inside'>
                                    <header>
                                        <h1>Detalhes</h1>
                                        <button onClick={() => window.history.back()}>
                                            Voltar
                                        </button>
                                    </header>
                                    <div className="data-container">
                                        <div className="category">
                                            <h2>Informações da Ocorrência</h2>
                                            <div className="info-container">
                                                <span>Número do BO:</span>
                                                <span>
                                                    {this.state.response[0].properties.numeroBOA}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Natureza do Acidente:</span>
                                                <span>
                                                    {this.state.response[0].properties.natureza}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Classificação do Acidente:</span>
                                                <span>
                                                    {this.state.response[0].properties.classificacaoAcidente}
                                                </span>
                                            </div>
                                        </div>
    
                                        <div className="category">
                                            <h2>Informações do Local</h2>
                                            <div className="info-container">
                                                <span>Caracteristica do local:</span>
                                                <span>
                                                    {this.state.response[0].properties.caracteristicaLocal}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Composição da Via:</span>
                                                <span>
                                                    {this.state.response[0].properties.composicaoPista}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Condição do Tempo:</span>
                                                <span>
                                                    {this.state.response[0].properties.condicaoTempo}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Velocidade da Via:</span>
                                                <span>
                                                    {this.state.response[0].properties.velocidade}
                                                </span>
                                            </div>
                                        </div>
    
                                        <div className="category">
                                            <h2>Data e Horário</h2>
                                            <div className="info-container">
                                                <span>Data da Ocorrência:</span>
                                                <span>
                                                    {this.state.response[0].properties.data}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Dia da Semana:</span>
                                                <span>
                                                    {this.state.response[0].properties.diaSemana}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Horário da Ocorrencia:</span>
                                                <span>
                                                    {this.state.response[0].properties.horaAcidente}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Horário do Registro:</span>
                                                <span>
                                                    {this.state.response[0].properties.horaRegistro}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Luminosidade:</span>
                                                <span>
                                                    {this.state.response[0].properties.luminosidade}
                                                </span>
                                            </div>
                                        </div>
    
                                        <div className="category">
                                            <h2>Informações Geográficas</h2>
                                            <div className="info-container">
                                                <span>Latitude:</span>
                                                <span>
                                                    {this.state.response[0].properties.latitude}
                                                </span>
                                            </div>
                                            <div className="info-container">
                                                <span>Longitude:</span>
                                                <span>
                                                    {this.state.response[0].properties.longitude}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                : null
                        }
                    </div>
                    <img className='loading' src="/images/loading.svg" alt="loading" />
                </React.Fragment>
            );
        }
    }
}