import React, { Component } from 'react';

import './_Select.scss';

export default class CustomSelect extends Component {
    constructor(props) {
        super(props);

        this.state = {
            options: this.props.options,
            value: this.props.value,
            id: this.props.id,
            onClick: this.props.onClick,
            title: this.props.title,
            visibleDropDown: -1,
        }

        this.state.onClick = this.state.onClick.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleToggleClass = this.handleToggleClass.bind(this);
        this.handleToggleClassOnMouseLeave = this.handleToggleClassOnMouseLeave.bind(this);
    }

    handleChange(e) {
        const inputValue = document.getElementById(this.state.id).value;

        e.preventDefault( );
        this.setState({ value: inputValue });
    }

    handleToggleClass( ) {
        const element = document.getElementById(`dropdown-${this.state.id}`);
        element.classList.toggle('hide');
    }

    handleToggleClassOnMouseLeave( ) {
        const element = document.getElementById(`dropdown-${this.state.id}`);
        element.classList.add('hide');
    }


    render() {

        const options = this.state.options.map((option) => {
            return (
                <span key={option}
                    onClick={( ) => {
                        this.state.onClick(this.state.id, option);
                        this.setState({ value: option });
                    }}>
                    {JSON.stringify(option).replace(/"/g, "")}
                </span>
            );
        });

        return (
            <div className='select-container' onMouseLeave={this.handleToggleClassOnMouseLeave}
                onMouseOver={this.showPosition}>
                <span className='select-title'>{this.state.title}</span>
                <input
                    readOnly
                    autoComplete='off'
                    onClick={this.handleToggleClass}
                    id={this.state.id}
                    value={this.state.value}
                    onChange={this.handleChange}
                />
                <div
                    className='options-dropdown hide'
                    onClick={this.handleToggleClass}
                    id={`dropdown-${this.state.id}`}>
                    {options}
                </div>
            </div>
        );
    }
}