import React, { useState, useEffect } from 'react';
import { Container, ButtonsContainer, Button, SecondaryContainer, InnerHeader, LinksContainer, ReadyButton, BackButton } from './Style';
import Header from '../../components/header/Header';

export default () => {

    const [database, setDatabase] = useState(null);
    const [map, setMap] = useState(null);
    const [dbSelected, setDbSelected] = useState(false);
    const [mapSelected, setMapSelected] = useState(false);

    const traslateDownDatabasesContainer = () => {
        const container = document.getElementById('dbs-container');
        container.style.marginTop = 'calc((100vh - 64px) * -1)';
    }

    const traslateUpDatabasesContainer = () => {
        const container = document.getElementById('dbs-container');
        container.style.marginTop = '0%';
    }

    useEffect(() => {
        console.log(mapSelected);
    }, [mapSelected]);

    return (
        <Container id='container'>
            <Header />
            <SecondaryContainer id='dbs-container'>
                <ButtonsContainer id='databases'>
                    <InnerHeader>
                        <h1>Selecione Uma Das Base de Dados</h1>
                        <h2>
                            Base Selecionada:
                            <span>
                                {
                                    database === 'accidents' ? 'Acidentes de Trânsito' :
                                        (database === 'medical-occurrences' ? 'Ocorrências Médicas' : 'Nenhum')
                                }
                            </span>
                        </h2>
                    </InnerHeader>
                    <Button
                        id='accidents'
                        onClick={() => {
                            setDbSelected(true)
                            setDatabase('accidents')
                        }}>
                        Acidentes de Trânsito
                    </Button>
                    <Button
                        id='medical-corrances'
                        onClick={() => {
                            setDbSelected(true)
                            setDatabase('medical-occurrences')
                        }}>
                        Ocorrências Médicas
                    </Button>
                    <Button
                        id='db-selected'
                        enabled={dbSelected}
                        disabled={!dbSelected}
                        onClick={traslateDownDatabasesContainer}>
                        Próximo
                    </Button>
                </ButtonsContainer>
            </SecondaryContainer>
            <SecondaryContainer>
                <ButtonsContainer id='maps'>
                    <InnerHeader>
                        <h1>Selecione o Tipo de Mapa Para Visualização</h1>
                        <h2>
                            Mapa Selecionado:
                            <span>
                                {
                                    (map === 'heat' ? 'Mapa de Calor' :
                                        (map === 'point' ? 'Mapa de Pontos' :
                                            (map === 'cluster' ? 'Mapa de Cluster' : 'Nenhum')))
                                }
                            </span>
                        </h2>
                    </InnerHeader>
                    <Button
                        onClick={() => {
                            setMap('heat')
                            setMapSelected(true)
                        }}>
                        <span>Mapa de Calor</span>
                    </Button>
                    <Button
                        onClick={() => {
                            setMap('point')
                            setMapSelected(true)
                        }}>
                        <span>Mapa de Pontos</span>
                    </Button>
                    <Button
                        onClick={() => {
                            setMap('cluster')
                            setMapSelected(true)
                        }}>
                        <span>Mapa de Cluster</span>
                    </Button>
                    <LinksContainer>
                        <ReadyButton href={mapSelected ? `/${database}/${map}` : "#"}
                            enabled={mapSelected}
                            database
                            map
                        >
                            Visualizar
                        </ReadyButton>
                        <BackButton onClick={traslateUpDatabasesContainer}>
                            Voltar
                        </BackButton>
                    </LinksContainer>
                </ButtonsContainer>
            </SecondaryContainer>
        </Container>
    );
}