import React, { Component } from 'react';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet.markercluster/dist/leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';
import 'leaflet.markercluster/dist/MarkerCluster.Default.css';
import { MapContainer } from '../../grid/Grid';
import FiltersMenu from '../filtersMenu/FiltersMenu';
import Map from '../Map';

const mapURL = 'https://maps.wikimedia.org/osm-intl/{z}/{x}/{y}{r}.png';

export default class ClusterMap extends Component {
    constructor(props) {
        super(props);

        this.state = {
            geoJsonData: [],
            mapType: 'cluster',
            center: [-5.192435, -37.339247],
            radius: 80,
        }

        this.renderMap = this.renderMap.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.refreshMap = this.refreshMap.bind(this);
    }

    handleChange(e) {
        e.preventDefault();

        this.setState({ [e.target.name]: e.target.value });
    }

    setPointProperties(feature, coordenates = this.state.geoJsonData.geometry.coordinates) {
        const circleMarkerOptions = {
            radius: 7,
            fillColor: '#0074e8', 
            fillOpacity: 0.659, 
            stroke: true,
            weight: 1,
            color: '#0074e8',
        }

        return L.circleMarker(coordenates, circleMarkerOptions);
    }

    createPopUp(numAccident, lt, lg, numMedical, data, hora, key) {
        if(numMedical === undefined) {
            const popUpContent = `
            <div>
                <div>
                    <span>Número:</span> 
                    <span>${ numAccident }</span>
                </div>
                <div>
                    <span>Latitude:</span>  
                    <span>${ lt }</span>
                </div>
                <div>
                    <span>Longitude:</span> 
                    <span>${ lg }</span>
                </div>
                <div>
                    <a href='/details/${ this.props.match.params.database }/${ key }'>Detalhes</a>
                </div>
            </div>
        `;

            return popUpContent;
        }
        else{
            const popUpContent = `
            <div>
                <div>
                    <span>Número:</span> 
                    <span>${ numMedical }</span>
                </div>
                <div>
                    <span>Data:</span>  
                    <span>${ data }</span>
                </div>
                <div>
                    <span>Hora:</span>  
                    <span>${ hora }</span>
                </div>
                <div>
                    <a href='/details/${ this.props.match.params.database }/${ key }'>Detalhes</a>
                </div>
            </div>
        `;

            return popUpContent;
        }
    }

    createMarker() {
        const marker = {
            pointToLayer: this.setPointProperties,
            onEachFeature: (feature, layer) => {
                const { numeroBOA, numero, latitude, longitude, data, hora } = feature.properties;
                const { _key } = feature;

                const popUpContent = this.createPopUp(
                    numeroBOA, latitude, longitude, numero, data, hora, _key
                );

                layer.bindPopup(popUpContent);
            }
        }
        return marker;
    }

    initMap(mapZoom) {
        if (this.map) {
            this.map.remove();
        }

        this.map = L.map('map', {
            center: this.state.center,
            zoom: mapZoom,
            minZoom: 5,
            maxZoom: 17,
            zoomControl: false,
            zoomSnap: .5,
        });

        L.tileLayer(mapURL, {
            detectRetina: true,
            maxZoom: 18,
            updateWhenZooming: false,
            updateWhenIdle: true,
        }).addTo(this.map);
    }

    renderMap(geoJsonData) {

        let customMarker = this.createMarker();

        this.setState({ geoJsonData: geoJsonData });
        this.initMap(13);

        const clusterLayer = L.markerClusterGroup({
            maxClusterRadius: Number(this.state.radius)
        });

        const pointsLayer = L.geoJSON(geoJsonData, customMarker);

        clusterLayer.addLayer(pointsLayer);
        clusterLayer.addTo(this.map);
    }

    refreshMap() {
        this.map.eachLayer(function (layer) {
            layer.remove();
        });

        const currentZoom = this.map.getZoom();

        this.initMap(currentZoom);

        const newClusterLayer = L.markerClusterGroup({
            maxClusterRadius: Number(this.state.radius)
        });

        let customMarker = this.createMarker();
        const pointsLayer = L.geoJSON(this.state.geoJsonData, customMarker);

        newClusterLayer.addLayer(pointsLayer);
        newClusterLayer.addTo(this.map);
    }

    componentDidMount() {
        this.initMap(13);
    }

    render() {
        return (
            <MapContainer>
                <FiltersMenu
                    showMobileMenu={this.state.showMobileMenu}
                    renderMap={this.renderMap}
                    refreshMap={this.refreshMap}
                    handleChange={this.handleChange}
                    mapRadius={this.state.radius}
                    mapType={this.state.mapType}
                    match={this.props.match}
                />
                <Map />
            </MapContainer>
        );
    }
}