import styled from 'styled-components';

const HeaderContainer = styled.header`
    min-width: 100vw;
    width: 100vw;
    padding: 5px 0;
    background: #20232a;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    z-index: 10;

    * { margin: auto 40px };

    .login-button{
        width: 100px;
        font-size: 17px;
        color: #f7f7f7;
        border: 1px solid #0074e8;
        background: #0074e8;
        border-radius: 2px;
        padding: 10px 0px;
        text-align: center;
        transition: all 200ms;

        &:hover {
            border-color: #1d5a97;
            background: #1d5a97;
        }

        @media(max-width: 568px){
            width: 80px;
            font-size: 16px;
        }
    }

    @media(max-width: 768px){
        * { margin: auto 20px }
    }

    @media(max-width: 568px){
        * { margin: auto 10px }
    }
`;

const Logo = styled.div`
    background: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;

    img{
        width: 50px;
        height: 50px;
        margin: 4px 0 0 0;
        padding: 0;
    }

    h1{
        margin: 0;
        padding: 0;
        font-weight: 400;
        font-size: 30px;
        color: #f7f7f7;
    }

    &:focus{ outline: 0 }

    @media(max-width: 768px){
        * { margin: auto 20px }
    }

    @media(max-width: 568px){
        * { margin: auto 10px }
    }
`;

export { HeaderContainer, Logo };