import React from 'react';

import './_Date.scss';

export default (props) => {
    return(
        <div className="date-container">
            <span className='date-title'>{props.title}</span>
            <input 
                name={props.name} 
                type="date" 
                defaultValue={props.value} 
                onChange={props.onChange}
            />
        </div>
    ); 
}