import React, { Component } from 'react';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet.heat/dist/leaflet-heat.js';
import { MapContainer } from '../../grid/Grid';
import FiltersMenu from '../filtersMenu/FiltersMenu';
import Map from '../Map';

const mapURL = 'https://maps.wikimedia.org/osm-intl/{z}/{x}/{y}{r}.png';

export default class PointMap extends Component {
    constructor(props) {
        super(props);

        this.state = {
            geoJsonData: [],
            mapType: 'heat',
            center: [-5.192435, -37.339247],
            radius: 20,
            opacity: 0.2,
        }

        this.renderMap = this.renderMap.bind(this);
        this.refreshMap = this.refreshMap.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    initMap(mapZoom) {
        if (this.map) {
            this.map.remove();
        }

        this.map = L.map('map', {
            center: this.state.center,
            zoom: mapZoom,
            minZoom: 5,
            maxZoom: 17,
            zoomControl: false,
            zoomSnap: .5,
        });

        L.tileLayer(mapURL, {
            detectRetina: true,
            maxZoom: 18,
            updateWhenZooming: false,
            updateWhenIdle: true,
        }).addTo(this.map);
    }

    renderMap(geoJsonData) {

        this.initMap(13);
        this.setState({ geoJsonData: geoJsonData });
        
        const heatLayer = L.heatLayer(geoJsonData, {
            radius: Number(this.state.radius),
            minOpacity: Number(this.state.opacity),
            max: 1,
        });
        heatLayer.addTo(this.map);
    }
    
    refreshMap( ){
        this.map.eachLayer(function(layer) {
            layer.remove( );
        });

        const currentZoom = this.map.getZoom( );

        this.initMap(currentZoom);

        const newHeatLayer = L.heatLayer(this.state.geoJsonData, {
            radius: Number(this.state.radius),
            minOpacity: Number(this.state.opacity),
        });

        newHeatLayer.addTo(this.map);
    }

    handleChange(e) {
        e.preventDefault();

        this.setState({ [e.target.name]: e.target.value });
    }

    componentDidMount() {
        this.initMap(13);
    }

    render() {
        return (
            <MapContainer>
                <FiltersMenu
                    refreshMap={this.refreshMap}
                    handleChange={this.handleChange}
                    mapRadius={this.state.radius}
                    mapOpacity={this.state.opacity}
                    mapType={this.state.mapType}
                    renderMap={this.renderMap} 
                    match={this.props.match}
                />
                <Map />
            </MapContainer>
        );
    }
}