import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Cookies from 'js-cookie';

import './_Home.scss';

export default class Home extends Component {

    constructor(props) {
        super(props);

        this.state = {
            logged: true,
        }
    }

    componentDidMount() {
        const token = Cookies.get('token');
        
        token === undefined
            ? this.setState({ logged: false })
            : this.setState({ logged: true });
    }

    logOut() {
        Cookies.remove('token');
        localStorage.removeItem('username');
        window.location.reload( );
    }

    render() {
        return (
            <div className='home-page'>
                <header>
                    <Link to='/'>
                        <img src="images/logo.svg" alt="Mapp" />
                        <h1>Mapp</h1>
                    </Link>
                    <Link to={this.state.logged ? '/' : '/login'}
                        onClick={this.state.logged ? this.logOut : null}>
                        {this.state.logged ? 'Sair' : 'Entrar'}
                    </Link>
                </header>
                <div className="home-content">
                    <div className="home-description">
                        <h1>Mapp</h1>
                        <p>Uma aplicação para visualização de base de dados georreferenciados</p>
                    </div>
                    <nav>
                        <Link to='/heat-map'>
                            <span>Mapa de Calor</span>
                            <span>Apresenta a densidade de pontos como manchas de calor determinadas por um raio</span>
                        </Link>
                        <Link to='/point-map'>
                            <span>Mapa de Pontos</span>
                            <span>Apresenta cada dado georreferenciado como um ponto no mapa</span>
                        </Link>
                        <Link to='/cluster-map'>
                            <span>Mapa de Cluster</span>
                            <span>Agrupa pontos próximos para uma melhor visualização</span>
                        </Link>
                    </nav>
                </div>
            </div>
        );
    }
}