import styled from 'styled-components';

const Container = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #20232a;

    * { box-sizing: border-box; user-select: none; }
`;

const HomeContent = styled.div`
    position: relative;
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    padding-bottom: 90px;
    z-index: 2;

    
    @media(max-width: 768px){
        width: 100%;
        padding-bottom: 0;
        align-items: center;
        justify-content: center;
    }

    @media(max-width: 568px){
        margin: 0;
    }
`;

const MobileBackground = styled.img`
    height: 100%;
    width: 100%;
    position: absolute;
    transform: scale(1.3) translate(-30%, -10%);
    opacity: .1;
    transition: 600ms;
    z-index: 1;
    display: none;
    
    @media(max-width: 768px){
        display: block;
    }
`;

const Title = styled.h1`
    pointer-events: none;
    text-align: left;
    font-size: 85px;
    color: #fff;
    font-weight: 300;
    margin: 0;
    text-transform: uppercase;
    margin-left: 80px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    img{
        margin: 0 10px 0 0;
        padding: 0;
        transform: scale(1.5);

        @media(max-width: 768px){
            transform: scale(1);
            margin-right: -15px;
        }

        @media(max-width: 568px){
            transform: scale(.9);
            margin-right: -22px;
        }
    }

    @media(max-width: 768px){
        margin: 0 0 -20px 0;
        font-size: 55px;
    }

    @media(max-width: 568px){
        font-size: 50px;
    }
`;

const Subtitle = styled.h2`
    pointer-events: none;
    text-align: left;
    font-size: 20px;
    color: #fff;
    font-weight: 300;
    margin: 0;
    margin-left: 80px;

    @media(max-width: 768px){
        margin-left: 0;
        font-size: 19px;
        text-align: center;
    }

    @media(max-width: 568px){
        font-size: 16px;
    }
`;

const Buttons = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin: 30px 0 0 80px;
    padding: 0;
    z-index: 5;

    @media(max-width: 768px){
        margin: 25px 0 0 0;
    }
`;

const Button = styled.a`
    border: none;
    border-radius: 3px;
    padding: 12px 0;
    font-size: 15px;
    transition: 300ms;
    background: ${props => props.bg};
    color: ${props => props.color};
    border: 1px solid ${props => props.borderColor};
    padding: 11px 80px;
    margin-right: 0 10px;
    text-decoration: none;
    cursor: pointer;

    &:hover{
        background: ${props => props.bgHover};
        color: ${props => props.colorHover};
        border-color: ${props => props.bgHover};
    }

    & + a{
        margin-left: 20px;
    }
    
    @media(max-width: 568px){
        padding: 12px 50px;
    }

`;

const HomeImage = styled.div`
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media(max-width: 768px){
        display: none;
    }
`;

const Image = styled.img`
    height: 100%;
    width: 100%;
    transform: scale(2) translateX(-25%);
    opacity: .2;
    transition: 600ms;
`;

export { Container, HomeContent, Title, Subtitle, Buttons, Button, HomeImage, Image, MobileBackground };