import React, { Component } from 'react';
import Range from '../../../formComponents/Range';
import Button from '../../../formComponents/Button';

import './_Preferences.scss';

export default class MapSettings extends Component {

    constructor(props) {
        super(props);

        this.state = {
            mapType: this.props.mapType,
            mapRadius: this.props.mapRadius || null,
            mapOpacity: this.props.mapOpacity || null,
            handleChange: this.props.handleChange || null,
            refreshMap: this.props.refreshMap || null,
            changeTheme: this.props.changeTheme || null,
            theme: this.props.theme
        }
    }

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    render() {
        return (
            <div className='map-settings'>
                <h2>Preferências</h2>
                {this.state.mapType === 'heat'
                    ? <Range
                        refreshMap={this.state.refreshMap}
                        handleChange={this.state.handleChange}
                        value={this.state.mapRadius}
                        onChange={this.handleChange}
                        mapType={this.state.mapType}
                        name='radius'
                        min='10' max='100'
                        step='5' title='Raio'
                    />
                    : null
                }

                {this.state.mapType === 'heat'
                    ? <Range
                        refreshMap={this.state.refreshMap}
                        mapType={this.state.mapType}
                        handleChange={this.state.handleChange}
                        name='opacity'
                        value={this.state.mapOpacity}
                        min='0.1' max='1.0'
                        step='0.1' title='Opacidade'
                        onChange={this.handleChange}
                    />
                    : null
                }

                {this.state.mapType === 'cluster'
                    ? <Range
                        refreshMap={this.state.refreshMap}
                        mapType={this.state.mapType}
                        handleChange={this.state.handleChange}
                        name='radius'
                        value={this.state.mapRadius}
                        min='10' max='170'
                        step='10' title='Raio'
                        onChange={this.handleChange}
                    />
                    : null
                }

                <div className="change-theme-container">
                    <span className='change-theme-title'>Tema</span>
                    <div className='buttons-container'>
                        <Button
                            class={`change-theme ${this.props.theme === -1 ? "selected" : 'unselected'} `}
                            onClick={( ) => this.state.changeTheme(-1) }
                            val='Escuro'
                        />
                        <Button
                            class={`change-theme ${this.props.theme === 1 ? "selected" : 'unselected'} `}
                            onClick={( ) => this.state.changeTheme(1) }
                            val='Claro'
                        />
                    </div>
                </div>
            </div>
        );
    }
}