import React, { Component } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';
import Form from './form/Form';
import Infos from './infos/Infos';
import Preferences from './preferences/Preferences';
import FiltersHeader from './filtersHeader/FiltersHeader';
import FiltersFooter from './filtersFooter/FiltersFooter';

import './_FiltersMenu.scss';

import { ip } from '../../../utils/api/url';

export default class FiltersMenu extends Component {

    constructor(props) {
        super(props);

        this.state = {
            mapRadius: this.props.mapRadius || null,
            mapOpacity: this.props.mapOpacity || null,
            handleChange: this.props.handleChange || null,
            refreshMap: this.props.refreshMap || null,

            mapType: this.props.mapType,
            database: this.props.match.params.database,
            theme: -1,

            filtering: false,
            timeElapsedFiltering: 0,
            registersAmount: 0,
            user: '',

            geoJsonData: [],
            apiURL: `${ip}${this.props.match.url}`
        }

        this.getServerData = this.getServerData.bind(this);
        this.changeTheme = this.changeTheme.bind(this);
        this.getUserLogged = this.getUserLogged.bind(this);
    }

    getServerData(formData) {

        this.setState({ filtering: true });

        let beginFilteringTime = Date.now();
        let endFilteringTime;
        let timeElapsedFiltering;
        let responseArray;

        const token = Cookies.get('token');

        axios.post(this.state.apiURL,
            JSON.stringify(formData),
            {
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: token
                },
                withCredentials: true,
            },
        ).then((res) => {
            console.log(res);
            responseArray = res.data.map((index) => {
                return index;
            });
        }).then(() => {
            this.setState({
                geoJsonData: responseArray,
                registersAmount: responseArray.length,
            });

            this.props.renderMap(this.state.geoJsonData)

        }).then(() => {
            endFilteringTime = Date.now();
            timeElapsedFiltering = Number((endFilteringTime - beginFilteringTime) / 1000);

            this.setState({
                filtering: false,
                timeElapsedFiltering: timeElapsedFiltering
            });
        }).catch((error) => {
            this.setState({ filtering: false });
            console.log(error);
        })
    }

    makeResizableDiv(divName) {

        const element = document.querySelector(divName);
        const resizer = document.querySelector('.resizer');

        resizer.addEventListener('mousedown', (e) => {
            e.preventDefault();
            window.addEventListener('mousemove', resize);
            window.addEventListener('mouseup', stopResize);
        });

        const resize = (e) => {
            element.style.width = e.pageX - element.getBoundingClientRect().left + 'px';
        }

        const stopResize = () => {
            window.removeEventListener('mousemove', resize);
        }
    }

    changeTheme(theme) {
        this.setState({ theme });
    }

    setResizerHeight() {
        const filtersMenu = document.querySelector('#filters-menu');
        const filtersMenuHeight = filtersMenu.scrollHeight;

        document.querySelector('.resizer').style.height = `${filtersMenuHeight}px`;
    }

    getUserLogged() {
        const user = localStorage.getItem('username');
        this.setState({user});
    }

    componentDidMount() {
        this.makeResizableDiv('#filters-menu');
        this.setResizerHeight();

        this.getUserLogged();

        console.log(this.props.match.params);
    }

    render() {
        return (
            <div
                id='filters-menu'
                className={this.state.theme === 1 ? "light-theme" : 'dark-theme'}>

                <FiltersHeader 
                    database={this.state.database}
                />

                <Form
                    getServerData={this.getServerData}
                    filtering={this.state.filtering}
                    mapType={this.state.mapType}
                    database={this.props.match.params.database}
                />

                <Preferences
                    mapRadius={this.state.mapRadius}
                    mapOpacity={this.state.mapOpacity}
                    refreshMap={this.state.refreshMap}
                    handleChange={this.state.handleChange}
                    mapType={this.state.mapType}
                    changeTheme={this.changeTheme}
                    theme={this.state.theme}
                />

                <Infos
                    mapType={this.state.mapType}
                    timeElapsedFiltering={this.state.timeElapsedFiltering}
                    registersAmount={this.state.registersAmount}
                    database={this.props.match.params.database}
                />

                <FiltersFooter user={this.state.user} />

                <div className="resizer"></div>
            </div>
        );
    }
}