import React from 'react';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

import ClusterMap from '../components/map/maps/ClusterMap';
import PointMap from '../components/map/maps/PointMap';
import HeatMap from '../components/map/maps/HeatMap';
import HomeOld from '../components/home/Home';
import Details from '../components/details/Details';
import Login from '../components/authentication/Login';
import VerifyAuthentication from '../components/authentication/VerifyAuthentication';

/*refatorando*/
import Home from '../pages/home/Home';
import ChoiceDataBase from '../pages/choiceDatabase/ChoiceDataBase';

const Routes = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path='/home-old' component={HomeOld} />
                <Route exact path='/login' component={Login} />
                <Route exact path='/:database/heat' component={ VerifyAuthentication(HeatMap) } />
                <Route exact path='/:database/cluster' component={ VerifyAuthentication(ClusterMap) } />
                <Route exact path='/:database/point' component={ VerifyAuthentication(PointMap) } />
                <Route exact path='/details/:database/:handle' component={ VerifyAuthentication(Details) } />

                <Route exact path='/databases' component={ChoiceDataBase} />
                <Route exact path='/' component={Home} />
            </Switch>
        </BrowserRouter>
    );
}

export default Routes;