import React from 'react';

import './_Grid.scss';

const MainContainer = (props) => {
    return (
        <div className='main-container'>
            {props.children}
        </div>
    );
}

const MapContainer = (props) => {
    return (
        <div className='map-container'>
            {props.children}
        </div>
    );
}

export { MainContainer, MapContainer };