import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Cookies from 'js-cookie';

import { HeaderContainer, Logo } from './Style';

import MappLogo from '../../assets/images/Logo.svg';

const Header = ( ) => {

    const [authenticated, setAuthenticated] = useState(null);

    const logOut = async () => {
        await Cookies.remove('token');
        await localStorage.removeItem('username');
    }

    useEffect(() => {
        Cookies.get('token') === undefined ? setAuthenticated(false) : setAuthenticated(true);
    }, []);

    return (
        <HeaderContainer>
            <Link to='/'>
                <Logo>
                    <img src={MappLogo} alt="mapp-logo" />
                    <h1>Mapp</h1>
                </Logo>
            </Link>
            <Link className='login-button'
                to={authenticated ? '/' : '/login'} onClick={authenticated ? logOut : null}>
                {authenticated ? 'Sair' : 'Entrar'}
            </Link>
        </HeaderContainer>
    );
}

export default Header;