import React from 'react';
import Button from '../formComponents/Button';

import './_LoginForm.scss';

export default (props) => {
    return (
        <div className="login-container">
            <div className='header'>
                <h1>Login</h1>
            </div>
            <form onSubmit={props.onSubmit}>
                <label htmlFor="username">
                    Usuário
                </label>
                <input
                    required
                    autoComplete='off'
                    type="text"
                    name='username'
                    value={props.username}
                    onChange={props.onChange}
                />
                <label htmlFor="password">
                    Senha
                </label>
                <input
                    required
                    autoComplete='off'
                    type="password"
                    name='password'
                    value={props.password}
                    onChange={props.onChange}
                />
                <Button
                    type='submit'
                    class='login-button'
                    val='Entrar'
                />
            </form>
            <span className={`login-status ${props.incorrectLogin ? 'show' : 'hide' }`}>
                {
                    props.incorrectLogin
                        ? props.incorrectLoginMessage === 'User Not Found'
                            ? 'Usuário não encontrado! Tente novamente'
                            : 'Senha incorreta! Tente novamente'
                        : null
                }
            </span>

            {
                props.loading 
                ? <img className='loading' src="/images/loading.svg" alt="logging"/> 
                : null
            }
        </div>
    );
}