import React from 'react';

import './_FiltersHeader.scss';

export default ({ database }) => {    
    return (
        <div className="filters-header">
            <h1>{
                    database === 'medical-occurrences' 
                    ? "Ocorrências Médicas"
                    : "Acidentes de  Trânsito"
                }
            </h1>
            <a href='/databases'>Voltar</a>
        </div>
    );
}