import React from 'react'

import './_Infos.scss';

export default (props) => {

    let mapType;
    let database;
    const city = 'Mossoró';

    switch(props.database){
        case 'medical-occurrences':  database = 'Ocorrências Médicas';
        break;
        case 'accidents':  database = 'Acidentes de Trânsito';
        break;
        default: console.log('unknown database');
    }

    switch(props.mapType){
        case 'heat': mapType = 'Mapa de Calor'; 
        break;
        case 'points': mapType = 'Mapa de Pontos'; 
        break;
        case 'cluster': mapType = 'Mapa de Cluster'; 
        break;
        default: console.log('unknown mapType');
        break;
    }

    return(
        <div className="infos">
            <h2>Informações</h2>
            <div className="info-row">
                <span className='info-key'>Cidade : </span>
                <span className='info-value'>{city}</span>
            </div>
            <div className="info-row">
                <span className='info-key'>Base de Dados : </span>
                <span className='info-value'>{database}</span>
            </div>
            <div className="info-row">
                <span className='info-key'>Tipo de Mapa : </span>
                <span className='info-value'>{mapType}</span>
            </div>
            <div className="info-row">
                <span className='info-key'>Quantidade de Registros : </span>
                <span className='info-value'>{props.registersAmount}</span>
            </div>
            <div className="info-row">
                <span className='info-key'>Tempo de Filtragem : </span>
                <span className='info-value'>{props.timeElapsedFiltering} s</span>
            </div>
        </div>
    );
}