import React, { Component } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';
import { Redirect, Link } from 'react-router-dom';
import LoginForm from './LoginForm';

import './_Login.scss';

import { ip } from '../../utils/api/url';

export default class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            username: '',
            password: '',
            incorrectLogin: false,
            incorrectLoginMessage: '',
            redirect: false,
            loading: false,

            apiUrl: `${ip}/login`,
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount() {
        const checkToken = Cookies.get('token');

        checkToken === undefined || checkToken === null
            ? this.setState({ redirect: false })
            : this.setState({ redirect: true });

        this.hideNoAuthMessage();
    }

    handleChange(e) {
        const name = e.target.name;
        const value = e.target.value;

        this.setState({ ...this.state, [name]: value });
    }

    handleSubmit = (e) => {
        const { history } = this.props;
        e.preventDefault();

        this.setState({ 
            loading: true,
            incorrectLogin: false
        });

        axios.post(this.state.apiUrl, {
            username: this.state.username,
            password: this.state.password,
        }).then((res) => {
            if (res.data.incorrectLogin === false) {
                Cookies.set('token', res.data.token);
                localStorage.setItem('username', res.data.username);

                if(this.props.noAuth){
                    setTimeout(() => {
                        window.location.reload( );
                    }, 800);
                }
                else{
                    setTimeout(() => {
                        history.push('/databases');
                    }, 800);
                }
            }
            else {
                setTimeout(() => {
                    this.setState({ incorrectLogin: true });
                
                    if (res.data.incorrectLoginMessage === 'User Not Found') {
                        this.setState({ 
                            incorrectLoginMessage: res.data.incorrectLoginMessage,
                            loading: false, 
                        });
                    }
                    else if (res.data.incorrectLoginMessage === 'Incorrect Password, Please Try Again') {
                        this.setState({ 
                            incorrectLoginMessage: res.data.incorrectLoginMessage,
                            loading: false, 
                        });
                    }  
                }, 600);
            }
        }).catch((error) => {
            this.setState({ loading: false });
            console.log(error);
        });
    }

    hideNoAuthMessage() {
        if (this.props.noAuth) {

            const noAuthMessage = document.querySelector('.no-auth');

            setTimeout(() => {
                noAuthMessage.classList.add('hide-message');
            }, 2000);
        }
    }

    componentDidUpdate( ){
        console.log(this.state.loading);
    }

    render() {

        const { redirect } = this.state;

        if (redirect) {
            return <Redirect to='/' />
        }
        else {
            return (
                <div className='login'>
                    <header>
                        <Link to='/'>
                            <img src="/images/logo.svg" alt="Mapp" />
                            <h1>Mapp</h1>
                        </Link>
                        <Link to='#'
                            onClick={
                                ( ) =>{
                                    const { history } = this.props;
                                    history.push('/');
                                }
                            }
                        >
                            Início
                        </Link>
                    </header>
                    <div className='login-content'>
                        <LoginForm
                            username={this.state.username}
                            password={this.state.password}
                            onSubmit={this.handleSubmit}
                            onChange={this.handleChange}
                            incorrectLogin={this.state.incorrectLogin}
                            incorrectLoginMessage={this.state.incorrectLoginMessage}
                            loading={this.state.loading}
                        />
                        {
                            this.props.noAuth
                                ? <span className='no-auth'>Faça login para continuar</span>
                                : null
                        }
                    </div>
                </div>
            );
        }
    }
}