import React, { useState, useEffect } from 'react';
import Cookies from 'js-cookie';
import {
    Container, HomeContent, Title,
    Subtitle, Buttons, Button, HomeImage, Image, MobileBackground
} from './Style';

import HomeBackground from '../../assets/images/HomeBackground.svg';
import Logo from '../../assets/images/Logo.svg';

const Home = (props) => {

    const [autenticated, setAutenticated] = useState(null);

    const logOut = async () => {
        await Cookies.remove('token');
        await localStorage.removeItem('username');
        await window.location.reload( );
    }

    const logIn = ( ) => {
        const { history } = props;
        history.push('/login');
    }

    useEffect(() => {
        const token = Cookies.get('token');
        token === undefined ? setAutenticated(false) : setAutenticated(true);
    }, []);

    return (
        <Container>
            <HomeContent>
                <MobileBackground src={HomeBackground} alt="mapp-mobile-home-background" />
                <Title><img src={Logo} alt="mapp-logo" />Mapp</Title>
                <Subtitle>Uma plataforma para visualização de base de dados georreferenciados</Subtitle>
                <Buttons>
                    <Button href="/databases"
                        bg='#0074e8'
                        borderColor='#0074e8'
                        color='#ffffff'
                        bgHover='#1d5a97'
                        colorHover='#ffffff'
                    >Iniciar</Button>
                    <Button href='#'
                        bg='trasparent'
                        borderColor='#ffffff'
                        color='#ffffff'
                        bgHover='#ffffff'
                        colorHover='#20232a'

                        onClick={ autenticated ? logOut: logIn }
                    >
                        {autenticated ? 'Sair' : 'Entrar'}
                    </Button>
                </Buttons>
            </HomeContent>
            <HomeImage>
                <Image src={HomeBackground} alt="mapp-home-background" />
            </HomeImage>
        </Container>
    );
}

export default Home;